.main-table{
    position:absolute;
    top:6cm;
    left:3cm;   
    margin:0.5cm 0 0 0.5cm;
    width:fit-content;
}

/**group bar 族栏*/
.group-bar{
    display:grid;
    grid-template-columns: repeat(18, 1fr);
    height:0.5cm;
    width:100%;
    position:absolute;
    top:-.5cm;
    left:0;
}
.t-group{
    font-size: 0.4cm;
    text-align:center;
    height: 100%;
}
/**periodic bar 周期栏*/
.periodic-bar{
    position:absolute;
    display:grid;
    grid-template-rows: repeat(10, 1fr);
    height:100%;
    width:0.5cm;
    left:-0.5cm;
    top:0;
}
.t-periodic{
    width:100%;
    text-align:center;
    display:flex;
    align-items:center;
}

/**main table*/
.t-row{
    display:block;
    white-space:nowrap;
    width:fit-content;
}

.control-board-box-wrapper{
    position:absolute;
    width:1000%;
    height:300%;
    z-index:999;
    font-size:16px;
    z-index:100;
}