
.header-wrapper{
    height:48px;
    display:flex;
    padding:0 24px;
    position:relative;
    background-color:#A7E4F2;
}
.product-name-wrapper{
    display:flex;
    margin:6px 0;
    align-items:center;
}
.header-logo{
    height:36px;
    width:36px;
    margin-right:6px;
    border-radius:50%;
}
.product-name{
    font-size:22px;
    font-weight:400;
    margin:0;
    letter-spacing:5px;
}