

.detail-wrapper-wrapper{
    position:fixed;
    top:0;
    left:0;
    width:100vw;
    height:100vh;
    display:flex;
    justify-content: center;
    background-image:radial-gradient(rgba(0,0,0,0.7),rgba(0,0,0,0.5));
    z-index: 999;
}   
.detail-wrapper{
    max-width:10cm;
    width:100%;
    height:100vh;
    top:0;
    background-color:#eee;
    overflow:scroll;
    /**firefox*/
    scrollbar-width:none;
    /**IE and edge*/
    -ms-overflow-style: none;
}
.detail-wrapper::-webkit-scrollbar{
    display:none;
}
.detail-image-card-wrapper-wrapper{
    position:relative;
    width:100%;
    padding-bottom:62%;
}
.detail-image-card-wrapper{
    position:absolute;
    top:0;left:0;right:0;bottom:0;
    color:#fff;
}
.detail-image-card-iamge{
    position:absolute;
    top:0;
    left:0;
    width:100%;
    height:100%;
    object-fit:cover;
}
.detail-image-card-filter{
    position:absolute;
    top:0;left:0;right:0;bottom:0;
    background-image:linear-gradient(0deg,rgba(0,0,0,0.7),transparent)
}
.detail-image-card-info-wrapper{
    position:absolute;
    display:grid;
    grid-template-rows:repeat(3,auto);
    grid-template-columns:repeat(3,auto);
    left:6%;
    bottom:6%;
    font-size:1.2cm;
}
.detail-image-card-symbol{
    grid-row: 1 / 3;
    font-size:100%;
    font-weight:700;
    padding-right:6px;

}
.detail-image-card-zh{
    font-size:36%;
    align-self:end;
    padding-right:6px;
}
.detail-image-card-number{
    font-size:36%;
    align-self:end;
}
.detail-image-card-en{
    grid-column: 2 / 4;
    font-size:36%;
}
.detail-image-card-atomic-mass{
    font-size:36%;
    grid-column:1 / 4;
}

.unit-title-box-wrapper{
    margin:3px 10px 0 10px;
    padding:0 6%;
    line-height:1.2cm;
    background-color:#fff;
    border-radius:5px;
    display:flex;
    justify-content:space-between;
    align-items:center;
}

.unit-item-box-wrapper{
    display:flex;
    flex-direction:column;
    margin:1px 10px 0 10px;
    padding:0 6%;
    line-height:0.6cm;
    background-color:#fff;
    border-radius:5px;
}
.unit-item-title{
    display:block;
    line-height:0.7cm;
    font-size:0.35cm;
}
.unit-item-data{
    display:block;
    line-height:0.5cm;
    font-size:0.3cm;
}
.unit-title-fold-icon{
    width:.6cm;
    height:0.6cm;
    margin-right:10px;
}
.unit-title-cion{
    width:.6cm;
    height:0.6cm;
    right:0;
}
.unit-title-icon-wrapper{
    display:flex;
    align-items:center;
}

.detail-electron-shells-wrapper{
    margin:1px 10px 0 10px;
    padding:0 6%;
    background-color:#fff;
    border-radius:5px;
    display:flex;
    justify-content:space-between;
    align-items:center;
    line-height:0.7cm;
    font-size:0.35cm;
    flex-direction:column;
}

.detail-element-spectral-wrapper{
    margin:1px 10px 0 10px;
    padding:0 6%;
    line-height:0.7cm;
    font-size:0.35cm;
    background-color:#fff;
    border-radius:5px;
    display:flex;
    justify-content:space-between;
    align-items:center;
    flex-direction:column;  
}

.detail-element-spectral-iamge{
    width:100%;
    margin-bottom:10px;
}

.detail-close-wrapper{
    position:relative;
    width:0;
}
.detail-close{
    position:absolute;
    top:20px;
    left:20px;
    width:1.2cm;
    height:1.2cm;
    border-radius:50%;
    background-color:#fff;
    font-size:1.2cm;
    line-height:1.2cm;
    text-align:center;
}

@media (max-width:12cm){
    .detail-close{
        right:20px !important;
        left:auto !important;
        width:0.6cm;
        height:0.6cm;
        font-size:0.6cm;
        line-height:0.6cm;
        z-index:999 !important;
        border:5px solid rgba(130,130,130,0.3);
    }
    .detail-wrapper{
        max-width:12cm !important; 
    }
}