
.e-element{
    display:inline-block;
    vertical-align:bottom;
    overflow:hidden;
    /*
    width: 120rpx;
    height: 140rpx;
    */
    width:1.4cm;
    height:1.6cm;
    font-size:1.4cm;
    /*background-color:#232328;
    color:#ffffff;
    */
    color:#333333;
    text-align:center;
    border-radius:3px;
    /*
    box-shadow: 0 0 0.5px #144a74 inset;
    outline:#dfdfdf solid 1px;
    */ 
}
.e-number{
    display:flex;
    align-items:center;
    justify-content:center;
    /*
    font-size: 24rpx;
    */
    font-size:20%;
    width:50%;
    height: 23%;
    border-radius:3px;
}
.e-symbol{
    display:block;
    /*
    font-size: 32rpx;
    */
    font-size:26%;
    top: 0%;
    height:29%;
}
.e-name{
    display:block;
    /*
    font-size: 32rpx;
    */
    font-size:26%;
    top: 0%;
    height:29%;
}
.e-bottomdata{
    display: flex;
    /*font-size: 18rpx;*/
    font-size:15%;
    justify-content: center;
    top: 0%;
}

/**交互*/
.focus{
    box-shadow:0 0 10px #888888;
}