.control-board-wrapper{
    position:absolute;
    display:flex;
    margin:20px;
    top:0;left:0;right:0;bottom:0;
    justify-content: center;
    align-items: center;
}
.control-board-element-explain-wrapper{
    display:grid;
    grid-template-columns:repeat(3,auto);
    grid-template-rows:repeat(4,1fr);
    width:fit-content;
    height:fit-content;
    width: -moz-fit-content;
    height: -moz-available;

}
.current-element-wrapper{
    grid-row: 1 / 5;
    display:flex;
    align-items:center;
    justify-content:center;
}
.img-line{
    width:0.6cm;
    height:100%;
    grid-column:2 / 3;
    grid-row:1 / 5;
}
.element-explain{
    grid-column: 3 / 4;
    text-align:center;
    padding:2px 5px;
    border-radius:0.5cm;
    border:0.5px solid #888888;
    font-size:0.3cm;
    margin:2px;
}
select{
    
}
.current-element{
    border:0.5px solid #333333;
}

/**tag*/
.control-board-tag-wrapper{
    margin:5px;
    display:flex;
    margin-left:20px;
}
.control-board-tag-group{
    margin:0 3px;
    display:flex;
    flex-direction: column;
    justify-content: center;
    text-align:center;
}
.control-board-tag{
    text-align:center;
    padding:2px 5px;
    border-radius:0.5cm;
    border:0.5px solid #888888;
    font-size:0.3cm;
    margin:2px;
}
.tag{
    box-shadow:0 0 10px #AC73FF;
    background-color:#AC73FF;
}
.control-board-tag-tip{
    width:1.2em;
    height:1.2em;
    font-size:1em;
    border:1px solid #888;
    color:#888;
    border-radius: 50%;
    align-self:center;
}